// src/components/Courses.js
import React, { useState, useEffect } from "react";
import "./Courses.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCalendarDays, faClock } from "@fortawesome/free-solid-svg-icons";
import testing from '../../assets/img/courses/testing.jpeg';
import java from "../../assets/img/courses/java.jpeg";
import aws from "../../assets/img/courses/AWS.jpg";
import angular from "../../assets/img/courses/angular.jpeg";
import azure from "../../assets/img/courses/azure.jpg";
import devops from "../../assets/img/courses/devops.jpg";
import dotnet from "../../assets/img/courses/dotnet.jpg";
import Python from "../../assets/img/courses/python.jpeg";
import sql from "../../assets/img/courses/mysql.jpeg";
import ui_ux from "../../assets/img/courses/ui_ux.webp";
import web from "../../assets/img/courses/Web.jpg";
import datasc from "../../assets/img/courses/datasc.jpg";
import react from "../../assets/img/courses/react.jpeg";
import { Modal, Button, Form } from "react-bootstrap";
import Aos from "aos";
import "aos/dist/aos.css";
import { useNavigate } from "react-router-dom";
import ReCAPTCHA from "react-google-recaptcha";

const Courses = () => {

  const courses = [

    {
      id: 1,
      title: "Java Course",
      description: "Java is a general-purpose, class-based, object-oriented programming language designed for having lesser implementation dependencies. It is a computing platform for application development.",
      image: java,
      duration: "180+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "10+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 2,
      title: "Python Course",
      description: "Python is a computer programming language often used to build websites and software, automate tasks, and conduct data analysis.",
      image: Python,
      duration: "150+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "11+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 3,
      title: "Dotnet Course",
      description: ".Net is a Microsoft web services strategy to connect information people, systems, and devices through software.",
      image: dotnet,
      duration: "100+hrs",
      weekdays: "1 hrs",
      weekend: "5hrs",
      mentor: "7+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 4,
      title: "DevOps Course",
      description: "DevOps is a set of practices, tools, and cultural philosophies.",
      image: devops,
      duration: "100+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "10+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 5,
      title: "AWS Course",
      description: "Amazon Web services are the world's most comprehensive and broadly adopted cloud platform, offering over 200 fully-featured services from data centers globally.",
      image: aws,
      duration: "150+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "10+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 6,
      title: "Azure Course",
      description: "Azure is a public cloud computing platform with solutions including Infrastructure as a service, Platform as a service, and software as a service.",
      image: azure,
      duration: "150+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "6+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 7,
      title: "Angular JS Course",
      description: "Angular JS was a JavaScript-based open-source front-end web framework for developing single-page applications.",
      image: angular,
      duration: "150+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "10+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 8,
      title: "UI/UX Course",
      description: "UX (User Experience) and UI (User interface) are two interdependent terms.",
      image: ui_ux,
      duration: "100+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "10+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 9,
      title: "MySQL Course",
      description: "MySQL is a relational database management system developed by Oracle that is based on structured query language (SQL).",
      image: sql,
      duration: "100+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "8+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 10,
      title: "Software Testing Course",
      description: "Selenium is an open-source tool that automates web browsers. Programming languages like Ruby, Java, Node JS, PHP, Perl, Python, and C#.",
      image: testing,
      duration: "100+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "11+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 11,
      title: "React.JS Course",
      description: "Selenium is an open-source tool that automates web browsers. Programming languages like Ruby, Java, Node JS, PHP, Perl, Python, and C#.",
      image: react,
      duration: "100+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "11+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 12,
      title: "Web Development",
      description: "Selenium is an open-source tool that automates web browsers. Programming languages like Ruby, Java, Node JS, PHP, Perl, Python, and C#.",
      image: web,
      duration: "100+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "11+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },
    {
      id: 13,
      title: "Data Science",
      description: "Selenium is an open-source tool that automates web browsers. Programming languages like Ruby, Java, Node JS, PHP, Perl, Python, and C#.",
      image: datasc,
      duration: "100+hrs",
      weekdays: "1 to 2 hrs",
      weekend: "5hrs",
      mentor: "11+yrs experience",
      trainingPattern: "Real-time training",
      mode: "Online/Offline",
      placementAssistance: "100% Placement assistance",
    },



  ];

  const navigate = useNavigate();
  const [showModal, setShowModal] = useState(false);
  const [filteredCourses, setFilteredCourses] = useState(courses);
  const [selectedCourse, setSelectedCourse] = useState(null);
  const [searchInput, setSearchInput] = useState("");
  const [showDropdown, setShowDropdown] = useState(false);
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    mobile: "",
    message: "",
    courses: "",
    captcha: false,
  });

  const [statusMessage, setStatusMessage] = useState("");
  const [captchaVerified, setCaptchaVerified] = useState(false);
  const [nextUrl, setNextUrl] = useState('');

  const handleModalClose = () => setShowModal(false);

  const handleModalOpen = (courseTitle) => {
    setSelectedCourse(courseTitle);
    setFormData((prevData) => ({
      ...prevData,
      courses: courseTitle,
    }));
    setShowModal(true);
  };

  const handleFormChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleSearchInput = (e) => {
    const input = e.target.value;
    setSearchInput(input);

    if (input === "") {
      setFilteredCourses(courses);
    } else {
      const filtered = courses.filter((course) =>
        course.title.toLowerCase().includes(input.toLowerCase())
      );
      setFilteredCourses(filtered);
    }
  };

  //captcha verifiy 
  const handleCaptchaChange = (value) => {
    if (value) {
      setCaptchaVerified(true); // Set to true when CAPTCHA is verified
    } else {
      setCaptchaVerified(false); // Set to false if not verified
    }
  };

  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const inputStyle = {
    padding: '10px',
    borderRadius: '5px',
    width: windowWidth <= 768 ? '71%' : '21%', // Change width based on window size
  };

  useEffect(() => {
    Aos.init({ duration: 1000 });
  }, []);

  
  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("Name:", formData.name);
    console.log("Mobile:", formData.mobile);
  
    try {
      const response = await fetch("/api/course", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });
  
      if (response.ok) {
        setStatusMessage("Your message has been successfully submitted!");
        setFormData({ name: "", email: "", mobile: "", courses: "" });
        navigate("/thank-you");
      } else {
        setStatusMessage("Something went wrong. Please try again later.");
      }
    } catch (error) {
      console.error(error);
      setStatusMessage("An error occurred. Please try again.");
    }
    handleModalClose(); 
   
  };

  return (
    <section id="courses" className="courses-section">
      <div className="container">
        <div className="row " data-aos="fade-up" data-aos-delay="100">
          <div className="col-md-12 text-center mb-0">
            <h2
              className="heading-section"
              style={{
                background: "linear-gradient(to right,#30cfd0, #330867)",
                WebkitBackgroundClip: "text",
                WebkitTextFillColor: "transparent",
              }}
            >
              Accelerate Your Learning Journey with Our Courses
            </h2>
          </div>

        </div>
        <div style={{ display: "flex", justifyContent: "end", marginBottom: "20px" }}>
          <input className="form-input"
            type="text"
            placeholder="Search or select a course"
            value={searchInput}
            onChange={handleSearchInput}
            list="course-options"
            // style={{ padding: "10px", borderRadius: "5px", width: "21%", }}
            style={inputStyle}
          />
          <datalist id="course-options"  >
            {courses.map((course) => (
              <option key={course.id} value={course.title} />
            ))}
          </datalist>
        </div>

        <div className="row">
          {filteredCourses.length > 0 ? (
            filteredCourses.map((course) => (
              <div key={course.id} className="col-12 col-sm-6 col-md-4 col-lg-3">
                <div className="card">
                  <div className="course-image">
                    <img
                      src={course.image}
                      alt={course.title}
                      className="card-img-top"
                      style={{ height: "200px", width: "100%" }}
                    />
                  </div>
                  <div className="card-body">
                    <h6 className="card-title" style={{ fontSize: "17px" }}>{course.title}</h6>
                    <ul className="course-details">
                      <li>
                        <FontAwesomeIcon icon={faClock} /> {course.duration}{" "}
                        <FontAwesomeIcon icon={faCalendarDays} /> {course.weekdays}
                      </li>
                      <li>
                        <a
                          href="#"
                          className="placement-assistance"
                          onClick={() => handleModalOpen(course.title)}
                        >
                          {course.placementAssistance}
                        </a>
                      </li>
                    </ul>
                    <button
                      type="submit"
                      className="btn btn-primary"
                      style={{
                        background: "linear-gradient(to right, #330867, #30cfd0)",
                        color: "#fff",
                        border: "none",
                        padding: "10px 20px",
                        fontSize: "16px",
                        fontWeight: "bold",
                        borderRadius: "0.25rem",
                        cursor: "pointer",
                        transition: "all 0.3s ease",
                      }}
                      onMouseEnter={(e) => {
                        e.target.style.background = "linear-gradient(to right, #30cfd0, #330867)";
                      }}
                      onMouseLeave={(e) => {
                        e.target.style.background = "linear-gradient(to right, #330867, #30cfd0)";
                      }}
                      onClick={() => handleModalOpen(course.title)}
                    >
                      Get Course Details
                    </button>
                    {statusMessage && <div className="sent-message mt-3">{statusMessage}</div>}
                  </div>
                </div>
              </div>
            ))
          ) : (
            <div
              style={{
                textAlign: "center",
                width: "100%",
                marginTop: "20px",
                fontSize: "18px",
                fontWeight: "bold",
                color: "red",
              }}
            >
              No details found
            </div>
          )}
        </div>
      </div>



      <Modal
        show={showModal}
        onHide={handleModalClose}

      >
        <Modal.Header closeButton >
          <Modal.Title style={{ color: "white", fontSize: "23px" }}>
            Download our syllabus on WhatsApp
            Fill now and get flat 5000  10000 Rs off on course fee.
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{

          color: 'white',
          padding: '20px'
        }}>
          <Form onSubmit={handleSubmit}
         >
        

            <Form.Group controlId="formName">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter your name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
              />
            </Form.Group>
            <Form.Group controlId="formEmail">
              <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter your email"
                name="email"
                value={formData.email}
                onChange={handleFormChange}
                required
              />
            </Form.Group>
            <Form.Group controlId="formMobile">
              <Form.Label>WhatsApp Number</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter your WhatsApp number"
                name="mobile"
                value={formData.mobile}
                onChange={(e) => {
                  const value = e.target.value;
                  // Allow only numeric characters and restrict to 10 digits
                  if (/^\d*$/.test(value) && value.length <= 10) {
                    handleFormChange(e); // Update form state only if valid
                  }
                }}
                maxLength="10" // Restrict input to 10 characters
                required
                className={
                  /^\d{10}$/.test(formData.mobile) ? "is-valid" : formData.mobile ? "is-invalid" : ""
                } // Add real-time validation styling
              />

            </Form.Group>

            <Form.Group controlId="formSelectedCourse">
              <Form.Label>Course Interested</Form.Label>
              <Form.Control
                type="text"
                name="courses"
                value={formData.courses}
                readOnly
              />
            </Form.Group>

            {/* reCAPTCHA Integration */}
            <div className="form-group mb-3">
              <ReCAPTCHA
                sitekey="6LdyK74qAAAAAHior1ETtBZRJf6-78AZFgkPYKf3"
                //  // Replace with your reCAPTCHA site key
                // sitekey="6LcwRb4qAAAAAMq-olseDFdPgiJ4s48v3oLdfMIW"
                onChange={handleCaptchaChange}
              />
            </div>

            <Button
              type="submit"
              style={{ background: "#7028e4" }}

            >  Submit
            </Button>

          </Form>
        </Modal.Body>
      </Modal>

      <style>
        {`
      .modal-content {
        background-image: linear-gradient(to bottom, #7028e4 0%, #e5b2ca 100%);
      }
    `}
      </style>

    </section>
  );
};

export default Courses;
