import React from "react";
import { Link } from "react-router-dom";
import logo from "../../assets/img/FAZO_ACADEMY_LOGO__2_-removebg-preview.png";

const Thankspage = () => {
  return (
    <div
      className="body-inner"
      style={{
        width: "100%",
        height: "100vh", 
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column", 
      }}
    >
      <a href="/">
        <img src={logo} alt="FAZO Academy Logo" width="120" />
      </a>
      <h1 className="m-t-50 font-weight-bold text-success">Thanking you</h1>
      <p className="lead">
        <strong>We'll be in contact soon!</strong>
      </p>
      <Link
        to="/"
        style={{
          backgroundColor: "blue",
          color: "white",
          margin: "20px",
          padding: "10px",
          borderRadius: "5px",
          textDecoration: "none",
        }}
      >
        Go to home page
      </Link>
    </div>
  );
};

export default Thankspage;
