import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import amazon from "../../assets/img/ourclient/amazon.webp";
import alphabet from "../../assets/img/ourclient/accenture.webp";
import paypal from "../../assets/img/ourclient/avasoft.webp";
import slack from "../../assets/img/ourclient/capegimin.webp";
import samsung from "../../assets/img/ourclient/cts.webp";
import google from "../../assets/img/ourclient/hcl.webp";
import fitbit from "../../assets/img/ourclient/syntel.webp";
import shopify from "../../assets/img/clients/client-1.png";
import stripe from "../../assets/img/clients/client-2.png";
import mapbox from "../../assets/img/clients/client-3.png";
import spotify from "../../assets/img/clients/client-4.png";
import uber from "../../assets/img/clients/client-5.png";
import airbnb from "../../assets/img/clients/client-6.png";
import netflix from "../../assets/img/clients/client-7.png";

import "./Client.css";

const Client = () => {
  const clients = [
    { src: amazon, alt: "Amazon" },
    { src: alphabet, alt: "Alphabet" },
    { src: paypal, alt: "Paypal" },
    { src: slack, alt: "Slack" },
    { src: samsung, alt: "Samsung" },
    { src: google, alt: "Google" },
    { src: fitbit, alt: "Fitbit" },
    { src: shopify, alt: "Shopify" },
    { src: stripe, alt: "Stripe" },
    // { src: mapbox, alt: "Mapbox" },
    // { src: spotify, alt: "Spotify" },
    // { src: uber, alt: "Uber" },
    // { src: airbnb, alt: "Airbnb" },
    // { src: netflix, alt: "Netflix" },
  ];

  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  const settings = {
    dots: true, // Enable indicators
    infinite: true,
    speed: 500,
    slidesToShow: 6, // Number of visible slides
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 4,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
        },
      },
    ],
  };

  return (
    <section className="clients-section">
      <div className="row justify-content-center" data-aos="fade-up" data-aos-delay="100">
        <div className="col-md-6 text-center mb-2">
          <h2
            className="heading-section"
            style={{
              background: "linear-gradient(to right,#30cfd0, #330867)",
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
            }}
          >
            Our Alumnis are working at
          </h2>
        </div>
      </div>
      <div className="container" style={{ marginTop:"20px"}}>
        <Slider {...settings}>
          {clients.map((client, index) => (
            <div key={index}>
              <div className="client-logo-container">
                <img
                  src={client.src}
                  alt={client.alt}
                  className="img-fluid u-clients"
                  style={{
                    maxHeight: "50px",
                    objectFit: "contain",
                    transition: "transform 0.3s ease, box-shadow 0.3s ease",
                  }}
                />
              </div>
            </div>
          ))}
        </Slider>
      </div>
    </section>
  );
};

export default Client;
