import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";
// import logo from "../../assets/img/Training/icon-training.svg";
import img1 from "../../assets/img/Training/9759911.png";
import img2 from "../../assets/img/Training/10061823.png";
import img3 from "../../assets/img/Training/1163412.png";
import img4 from "../../assets/img/Training/18566350.png";
import img5 from "../../assets/img/Training/2463510.png";
import img6 from "../../assets/img/Training/4739237.png";
import img7 from "../../assets/img/Training/9436966.png";
import img8 from "../../assets/img/Training/9750216.png";
import img9 from "../../assets/img/Training/9953136.png";
// import img from "../../assets/img/Training/10061823.png";



// Example data for images and headings
const cardData = [
  {
    imgSrc: img1,
    heading: "100% Placement Assurance",
  },
  {
    imgSrc: img2,
    heading: "One-to-one session",
  },
  {
    imgSrc: img3,
    heading: "Learn with working professionals",
  },
  {
    imgSrc: img4,
    heading: "Learn with Live Projects",
  },
  {
    imgSrc: img5,
    heading: "Online & Offline classes Available",
  },
  {
    imgSrc: img6,
    heading: "Flexible class timings",
  },
  {
    imgSrc: img7,
    heading: "Portfolio update",
  },
  {
    imgSrc: img8,
    heading: "Placement Training",
  },
];

const Training = () => {

  useEffect(() => {
    AOS.init({ duration: 1000 }); // Initialize AOS with options (duration in milliseconds)
  }, []);

  return (
    <div className="container my-5" style={{
      // background: "linear-gradient(to right, #330867, #30cfd0)",
    }}>
       <div className="row justify-content-center" data-aos="fade-up" data-aos-delay="100">
       <div className="col-md-6 text-center mb-2">
  <h2
    className="heading-section"
    style={{
      background: "linear-gradient(to right,#30cfd0, #330867)",
      WebkitBackgroundClip: "text",
      WebkitTextFillColor: "transparent",
    }}
  >
    Best Training Institute in Chennai
  </h2>
</div>

    </div>
      <div className="row">
        {cardData.map((card, index) => (
          <div className="col-12 col-sm-6 col-md-3 mb-4 p-1" key={index}>
            <div
              className="card text-center"
              style={{
                background: "linear-gradient(to right, #330867, #30cfd0)",
              }}
            >
              <div
                style={{
                  margin: "20px",
                  display: "flex",
                  justifyContent: "center",
                }}
              >
                <img src={card.imgSrc} alt="icon" width={80} height={80} />
              </div>
              <h5
                style={{
                  color: "white",
                  display: "flex",
                  justifyContent: "center",
                  fontSize: "14px",
                  fontWeight: 700,
                  textTransform: "uppercase",
                  marginBottom: "20px",
                }}
              >
                {card.heading}
              </h5>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Training;
