import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "./Header.css";
import Banner2 from "../../assets/img/banner/Comp 2_00000.jpg";
import Banner3 from "../../assets/img/banner/Comp 6_00000.jpg";
import Banner1 from "../../assets/img/banner/Pre-comp 7_00000.jpg";
import { Modal, Form, Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import ReCAPTCHA from "react-google-recaptcha";

const Header = () => {
  const courses = [
    { id: 1, title: "Java Course" },
    { id: 2, title: "Python Course" },
    { id: 3, title: "Dotnet Course" },
    { id: 4, title: "DevOps Course" },
    { id: 5, title: "AWS Course" },
    { id: 6, title: "Azure Course" },
    { id: 7, title: "Angular JS Course" },
    { id: 8, title: "UI/UX Course" },
    { id: 9, title: "MySQL Course" },
    { id: 10, title: "Software Testing Course" },
  ];
  const navigate = useNavigate();
  const [showModal, setShowModal] = useState(false);
  const [captchaVerified, setCaptchaVerified] = useState(false);
  const [statusMessage, setStatusMessage] = useState("");
  
  // const [nextUrl, setNextUrl] = useState('');
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    mobile: "",
    message: "",
    courses: "",
    captcha: false,
  });

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowModal(true);
    }, 3000);
    return () => clearTimeout(timer);
  }, []);
  // useEffect(() => {
  //   setNextUrl(`${window.location.origin}/thank-you`);
  // }, []);

  const handleModalClose = () => setShowModal(false);

  const handleFormChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: type === "checkbox" ? checked : value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    try {
      const response = await fetch("/api/header", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });
  
      if (response.ok) {
        setStatusMessage("Your message has been successfully submitted!");
        setFormData({ name: "", email: "", mobile: "", subject: "", message: "",});
        navigate("/thank-you");
      } else {
        setStatusMessage("Something went wrong. Please try again later.");
      }
    } catch (error) {
      console.error(error);
      setStatusMessage("An error occurred. Please try again.");
    }
  };
 
  //captcha verifiy 
  const handleCaptchaChange = (value) => {
    if (value) {
      setCaptchaVerified(true); // Set to true when CAPTCHA is verified
    } else {
      setCaptchaVerified(false); // Set to false if not verified
    }
  };

  return (
    <>
      <section className="hero-section">
        <div id="heroCarousel" className="carousel slide" data-bs-ride="carousel">
          <div className="carousel-indicators" >
            <button
              type="button"
              data-bs-target="#heroCarousel"
              data-bs-slide-to="0"
              className="active"
              aria-current="true"
              aria-label="Slide 1"
            >

            </button>
            <button
              type="button"
              data-bs-target="#heroCarousel"
              data-bs-slide-to="1"
              aria-label="Slide 2"
            ></button>
            <button
              type="button"
              data-bs-target="#heroCarousel"
              data-bs-slide-to="2"
              aria-label="Slide 3"
            ></button>
          </div>
          <div id="carouselExample" class="carousel slide" data-bs-touch="true">
          <div className="carousel-inner">
            <div className="carousel-item active">
              <img
                src={Banner1}
                className="d-block w-100"
                alt="Welcome"
              />
              <div className="carousel-caption d-none d-md-block">
                <div className="first-banner">
                  <a href="" className="btn btn-secondary" onClick={(e) => {
                    e.preventDefault();
                    setShowModal(true);
                  }}
                  >
                    Expert Training
                  </a>
                  <a href="" className="btn btn-secondary" onClick={(e) => {
                    e.preventDefault();
                    setShowModal(true);
                  }}>
                    Assured Placement
                  </a>
                </div>

              </div>

            </div>


            <div className="carousel-item ">

              <img
                src={Banner2}
                className="d-block w-100"
                alt="Explore Courses"
              />
              <div className="carousel-caption d-none d-md-block">
                <div className="secound-banner">
                  <div>
                    <a href="" className="btn btn-secondary" onClick={(e) => {
                      e.preventDefault();
                      setShowModal(true);
                    }}>
                      One-One Training
                    </a>
                    <a href="" className="btn btn-secondary" onClick={(e) => {
                      e.preventDefault();
                      setShowModal(true);
                    }}>
                      Expert Faculty
                    </a>
                  </div>
                  <div style={{ marginLeft: "42px" }}>
                    <a href="" className="btn btn-secondary" onClick={(e) => {
                      e.preventDefault();
                      setShowModal(true);
                    }}>
                      Real-Time Training
                    </a>
                    <a href="" className="btn btn-secondary" onClick={(e) => {
                      e.preventDefault();
                      setShowModal(true);
                    }}>
                      Placement Support
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="carousel-item">
              <img
                src={Banner3}
                className="d-block w-100"
                alt="Join Community"
              />

            </div>
          </div>
</div>
          
          <button
            className="carousel-control-prev"
            type="button"
            data-bs-target="#heroCarousel"
            data-bs-slide="prev"
            style={{ display: "none" }}
          >
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Previous</span>
          </button>
          <button
            className="carousel-control-next"
            type="button"
            data-bs-target="#heroCarousel"
            data-bs-slide="next"
            style={{ display: "none" }}
          >
            <span className="carousel-control-next-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Next</span>
          </button>
        </div>
      </section>

      <Modal show={showModal} onHide={handleModalClose}>
        <Modal.Header closeButton>
          <Modal.Title style={{ color: "white", fontSize: "25px" }}>
            Fill now and get flat 5000  10000 Rs off on course fee.
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ color: "white", padding: "20px" }}>
          <Form onSubmit={handleSubmit}>

          {/* <Form action="https://formsubmit.co/priya23sarath@gmail.com"
            method="POST"> */}

       

            <Form.Group controlId="formName">
              <Form.Label>Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter your name"
                name="name"
                value={formData.name}
                onChange={handleFormChange}
                required
              />
            </Form.Group>
            <Form.Group controlId="formEmail">
              <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter your email"
                name="email"
                value={formData.email}
                onChange={handleFormChange}
                required
              />
            </Form.Group>
            <Form.Group controlId="formMobile">
              <Form.Label>Mobile</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter your mobile number"
                name="mobile"
                value={formData.mobile}
                onChange={(e) => {
                  const value = e.target.value;
                  // Allow only numbers and limit to 10 digits
                  if (/^\d*$/.test(value) && value.length <= 10) {
                    handleFormChange(e); // Call the handler only if valid
                  }
                }}
                maxLength="10" // Prevent typing more than 10 characters
                required
                className={/^\d{10}$/.test(formData.mobile) ? "is-valid" : "is-invalid"} // Validation styling
              />
            
            </Form.Group>

            <Form.Group controlId="formSelectedCourse">
              <Form.Label>Course Interested</Form.Label>
              <Form.Control
                as="select"
                name="courses"
                value={formData.courses}
                onChange={handleFormChange}
                required
              >
                <option value="">Select a course</option>
                {courses.map((course) => (
                  <option key={course.id} value={course.title}>
                    {course.title}
                  </option>
                ))}
              </Form.Control>
            </Form.Group>
            <div className="form-group mb-4">
              <ReCAPTCHA
               sitekey="6LdyK74qAAAAAHior1ETtBZRJf6-78AZFgkPYKf3"
                // sitekey="6LcwRb4qAAAAAMq-olseDFdPgiJ4s48v3oLdfMIW"
                // Replace with your reCAPTCHA site key
                onChange={handleCaptchaChange}
              />
            </div>

            <Button type="submit" style={{ background: "#7028e4" }} >
              Submit
            </Button>
          </Form>
        </Modal.Body>
      </Modal>
    </>
  );
};

export default Header;

