import React, { useEffect, useState } from "react";
import "./Contact.css";
import Aos from "aos";
import "aos/dist/aos.css";
import { useNavigate } from "react-router-dom";
import ReCAPTCHA from "react-google-recaptcha"; // Import reCAPTCHA

// Importing the GIF
const videoUrl = require("../../assets/img/transparent.gif");

const Contact = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    subject: "",
    message: "",
    mobile: "",
  });
  const [statusMessage, setStatusMessage] = useState("");
  const [captchaVerified, setCaptchaVerified] = useState(false); // Track CAPTCHA verification
  const navigate = useNavigate();
  const [nextUrl, setNextUrl] = useState('');
  const [errors, setErrors] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  // Handle reCAPTCHA change event
  const handleCaptchaChange = (value) => {
    if (value) {
      setCaptchaVerified(true); // Set to true when CAPTCHA is verified
    } else {
      setCaptchaVerified(false); // Set to false if not verified
    }
  };

  useEffect(() => {
    Aos.init({ duration: 1000 }); 
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();
  
    try {
      const response = await fetch("/api/contact", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });
  
      if (response.ok) {
        setStatusMessage("Your message has been successfully submitted!");
        setFormData({ name: "", email: "", mobile: "", subject: "", message: "",});
        navigate("/thank-you");
      } else {
        setStatusMessage("Something went wrong. Please try again later.");
      }
    } catch (error) {
      console.error(error);
      setStatusMessage("An error occurred. Please try again.");
    }
  };
 
  return (
    <section className="contact-section">
      <div className="container">
        <div className="row justify-content-center" data-aos="fade-up" data-aos-delay="100">
          <div className="col-md-6 text-center mb-5">
            <h2
              className="heading-section"
              style={{
                background: "linear-gradient(to right,#30cfd0, #330867)",
                WebkitBackgroundClip: "text",
                WebkitTextFillColor: "transparent",
              }}
            >
              Contact Us
            </h2>
          </div>
        </div>

        <div className="row justify-content-center">
          {/* <div className="col-md-4">
            <iframe
                src="https://maps.app.goo.gl/W9jNi6VWGbfWmbB57"

              width="100%"
              height="200"
              style={{ border: "0", height: "550px" }}
              allowFullScreen=""
              aria-hidden="false"
              tabIndex="0"
            ></iframe>
          </div> */}

          <div className="col-md-8">
            <div className="wrapper">
              <div className="row no-gutters">
                <div className="col-md-6 d-flex align-items-stretch">
                  <div className="info-wrap bg-primary w-100 p-lg-5 p-4">
                    <h3 className="mb-4 text-white">Contact Us</h3>
                    <div className="info">
                      {/* Displaying the GIF */}
                      <div>
                        <img src={videoUrl} alt="Animated GIF" width="100%" height="auto" />
                      </div>

                      <div className="mb-3 d-flex align-items-center">
                        <p className="m-0">
                          <i
                            className="bi bi-geo-alt"
                            style={{ fontSize: "1.5rem", marginRight: "10px", color: "white" }}
                          ></i>
                          No:21 Vinayaga Nagar, Okiyam Thoraipakkam, Chennai - 600097.
                        </p>
                      </div>
                      <div className="mb-3 d-flex align-items-center">
                        <p className="m-0">
                          <i
                            className="bi bi-telephone"
                            style={{ fontSize: "1.5rem", marginRight: "10px", color: "white" }}
                          ></i>
                          +91 6380866249 <br />044 35670376
                        </p>
                      </div>
                      <div className="mb-3 d-flex align-items-center">
                        <p className="m-0">
                          <i
                            className="bi bi-envelope"
                            style={{ fontSize: "1.5rem", marginRight: "10px", color: "white" }}
                          ></i>
                          info@fazoacademy.com
                        </p>
                      </div>
                    </div>
                  </div>
                </div>

                <div
                  className="col-md-6 d-flex align-items-stretch"
                  style={{ border: "linear-gradient(to right, #7028e4, #e5b2ca)" }}
                >
                  <form className="contact-form w-100 p-lg-5 p-4" onSubmit={handleSubmit}>
                    <input
                      type="hidden"
                      name="_next"
                      value={nextUrl}
                    />
                    <input type="hidden" name="_template" value="table" />
                    <input type="hidden" name="_captcha" value="false" />

                    <h5 className="stylish-heading">If you have any queries, please contact us!</h5>

                    <div className="form-group mb-3">
                      <input
                        type="text"
                        className="form-control"
                        placeholder="Your Name"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        required
                      />
                    </div>
                    <div className="form-group mb-3">
                      <input
                        type="email"
                        className="form-control"
                        placeholder="Your Email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                      />
                    </div>
                    <div className="form-group mb-3">
                      <input
                        type="number"
                        className={`form-control ${errors.mobile ? "is-invalid" : "is-valid"}`}
                        placeholder="Your Mobile Number"
                        name="mobile"
                        value={formData.mobile}
                        onChange={(e) => {
                          const value = e.target.value;
                          if (value.length <= 10) {
                            handleChange(e); // Update state only if the input length is <= 10
                          }
                        }}
                        required
                      />
                      {errors.mobile && <small className="text-danger">{errors.mobile}</small>}
                    </div>

              

                <div className="form-group mb-3">
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Subject"
                    name="subject"
                    value={formData.subject}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="form-group mb-3">
                  <textarea
                    className="form-control"
                    rows="5"
                    placeholder="Message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    required
                  ></textarea>
                </div>

                {/* reCAPTCHA Integration */}
                <div className="form-group mb-3">
                  <ReCAPTCHA
                    // sitekey="6LcwRb4qAAAAAMq-olseDFdPgiJ4s48v3oLdfMIW"
                    sitekey="6LdyK74qAAAAAHior1ETtBZRJf6-78AZFgkPYKf3"
                    //  // Replace with your reCAPTCHA site key
                    onChange={handleCaptchaChange}
                  />
                </div>

                <div className="form-group">
                  <button
                    type="submit"
                    className="btn btn-primary"
                    style={{
                      background: "linear-gradient(to right, #330867, #30cfd0)",
                      color: "#fff",
                      border: "none",
                      padding: "10px 20px",
                      fontSize: "16px",
                      fontWeight: "bold",
                      borderRadius: "0.25rem",
                      cursor: "pointer",
                      transition: "all 0.3s ease",
                    }}
                    onMouseEnter={(e) => {
                      e.target.style.background = "linear-gradient(to right, #30cfd0, #330867)";
                    }}
                    onMouseLeave={(e) => {
                      e.target.style.background = "linear-gradient(to right, #330867, #30cfd0)";
                    }}
                  >
                    Send Message
                  </button>
                </div>

                {statusMessage && <div className="sent-message mt-3">{statusMessage}</div>}
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
      </div >
    </section >
  );
};

export default Contact;
