import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./Navbar.css";
import logo from '../../assets/img/FAZO_ACADEMY_LOGO__2_-removebg-preview.png';
import whatsapp from "../../assets/img/courses/whatspp-01.png";
import call from "../../assets/img/courses/call-answer-01.png";
import map from "../../assets/img/map.png";

const Navbar = () => {
  const [atBottom, setAtBottom] = useState(false);

  // Check if the user is at the bottom of the page
  const checkIfAtBottom = () => {
    const scrollPosition = window.scrollY;
    const windowHeight = window.innerHeight;
    const documentHeight = document.documentElement.scrollHeight;

    if (scrollPosition + windowHeight >= documentHeight) {
      setAtBottom(true);
    } else {
      setAtBottom(false);
    }
  };

  // Add event listener to track scroll position
  useEffect(() => {
    window.addEventListener('scroll', checkIfAtBottom);

    // Clean up event listener on unmount
    return () => {
      window.removeEventListener('scroll', checkIfAtBottom);
    };
  }, []);

  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light fixed-top p-0" style={{ backgroundColor: "white" }}>
        <div className="container">         
          <Link className="navbar-brand" to="/">
            <div>
              <img src={logo} alt="Logo" width="150" height="150" className="logo-section" />
            </div>
          </Link>
          <div className="button-container">
            <a href="tel:+916380866249" className="flip-button call-us">
              <div className="flip-inner">
                <div className="flip-front">
                  Call Us
                </div>
                <div className="flip-back">
                  +91 6380866249
                </div>
              </div>
            </a>
            <a href="mailto:dnskd@gmail.com" className="flip-button email-us">
              <div className="flip-inner">
                <div className="flip-front">
                  Email Us
                </div>
                <div className="flip-back">
                  info@fazoacademy.com
                </div>
              </div>
            </a>
            
          </div>
        </div>
      </nav>   

      <div className="main-icons">
        <ul className="first">
          <li className="color1">
            <a href="tel:+916380866249">
              <p>Call <br />+91 6380866249</p>
              <img src={call} width="20px" height="20px" className="img-fluid" />
            </a>
          </li>
          <li className="color2">
            <a href="https://api.whatsapp.com/send?phone=916380866249" target="_blank">
              <p>Whatsapp <br />+91 6380866249</p>
              <img src={whatsapp} width="24" height="24" className="img-fluid" />
            </a>
          </li>
          <li className="color3">
  <a
    href="https://maps.app.goo.gl/W9jNi6VWGbfWmbB57"
    target="_blank"
    rel="noopener noreferrer" // Best practice for opening new tabs
  >
    <p style={{ color:"black"}}>Location <br />Fazo Academy</p>
    <img src={map} width="30" height="30" className="img-fluid1" alt="Map Icon" />
  </a>
</li>

        </ul>
      </div>

      {/* Scroll Down Arrow */}
      <div
  className="scroll-arrow"
  style={{
    width: "50px",
    height: "50px",
    position: "fixed",
    bottom: "0px",
    right: "5px",
    backgroundColor: atBottom ? '#330867' : '#30cfd0', // Change color based on scroll position
    padding: '13px',
    borderRadius: '9px',
    cursor: 'pointer',
    transition: ' 1.3s ease', // Smooth background color transition
    display: "flex",
    justifyContent: "center",
    color: "white",
    zIndex: 1000,
  }}
  onClick={() => {
    if (!atBottom) {
      window.scrollTo({ top: document.documentElement.scrollHeight, behavior: 'smooth' });
    } else {
      window.scrollTo({ top: 0, behavior: 'smooth' });
    }
  }}
>
  {atBottom ? '' : ''}
</div>

    </>
  );
};

export default Navbar;
