import React, { useEffect } from "react";
// import services1 from "../../assets/img/services/serv_1.png";
import services1 from "../../assets/img/services/serv_2.png";
import services2 from "../../assets/img/services/Carrer.png";
import Services3 from "../../assets/img/services/school.png";
import Services4 from "../../assets/img/services/fresh.png";
import Services5 from "../../assets/img/services/carrergap.png";
import Services6 from "../../assets/img/services/Placeme.png";
import Services7 from "../../assets/img/services/project.png";



import "./Services.css";
import Aos from "aos";
import "aos/dist/aos.css";

const servicesData = [
    { id: 1, title: "Non-IT to IT Jobs", image: services1 },
    { id: 2, title: "Career Guidance", image: services2 },
    { id: 3, title: "School/College Students", image: Services3 },
    { id: 4, title: "Career Gap", image: Services5},
    { id: 5, title: "Fresher IT Job Seekers", image: Services4 },
    { id: 6, title: "Placement Support", image: Services6 },
    { id: 7, title: "Project Support", image: Services7 },
];

const Services = () => {

      useEffect(() => {
        Aos.init({ duration: 1000 }); 
      }, []);
    return (
        <>


            <div className="row justify-content-center" data-aos="fade-up" data-aos-delay="100">
                <div className="col-md-6 text-center mb-2">
                    <h2
                        className="heading-section"
                        style={{
                            background: "linear-gradient(to right,#30cfd0, #330867)",
                            WebkitBackgroundClip: "text",
                            WebkitTextFillColor: "transparent",
                        }}
                    >
                        Who are the people we serve?
                    </h2>
                </div>

            </div>
            <div className="container-fluid" style={{ backgroundColor: "#211255" }}>
                {servicesData.map((service) => (
                    <div
                        key={service.id}
                        className="service-box"
                        style={{ backgroundImage: `url(${service.image})` }}
                    >
                        <h3 className="service-heading">{service.title}</h3>
                    </div>
                ))}
            </div>
        </>
    );
};

export default Services;
